.select-option-container {
  line-height: 1;
}

.select-option {
  cursor: pointer;
  color: #708090;
  white-space: nowrap;
  border-radius: 6px;
  width: 100%;
  display: flex;
  position: relative;
  box-shadow: inset 0 0 0 1px #a9a9a9;
}

.select-option--open {
  box-shadow: inset 0 0 0 2px #1e90ff;
}

.select-option:not(.select-option--open):hover {
  box-shadow: inset 0 0 0 1px #696969;
}

.select-option .select-option-trigger {
  align-items: center;
  width: 100%;
  height: 48px;
  padding-left: 16px;
  padding-right: 16px;
  display: flex;
  position: relative;
}

.select-option .select-option-trigger:after {
  content: "";
  background: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0nMTAnIGhlaWdodD0nNScgdmlld0JveD0nNyAxMCAxMCA1JyB4bWxucz0naHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmcnPjxwYXRoIGZpbGwtcnVsZT0nZXZlbm9kZCcgb3BhY2l0eT0nLjU0JyBkPSdNNyAxMGw1IDUgNS01eicvPjwvc3ZnPg==") center / 12px 12px no-repeat;
  width: 12px;
  height: 12px;
  margin: auto;
  display: block;
  position: absolute;
  inset: 0 14px 0 auto;
}

.select-option--dense .select-option-trigger {
  height: 36px;
}

.select-option--open .select-option-trigger:after {
  transform: rotate(.5turn);
}

.select-option .select-option-items {
  z-index: 1000;
  background-color: #fff;
  border-radius: 6px;
  width: 100%;
  max-height: 210px;
  margin-top: 4px;
  padding-top: 8px;
  padding-bottom: 8px;
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  overflow: hidden visible;
  box-shadow: 0 5px 5px -3px #0003, 0 8px 10px 1px #00000024, 0 3px 14px 2px #0000001f;
}

.select-option .select-option-items::-webkit-scrollbar {
  opacity: 0;
  width: 12px;
}

.select-option .select-option-items::-webkit-scrollbar-thumb {
  background-color: var(--c-scrollbar-thumb);
  border: 4px solid var(--c-scrollbar-track);
}

.select-option .select-option-items::-webkit-scrollbar-thumb:hover {
  background-color: var(--c-scrollbar-hover);
}

.select-option .select-option-items::-webkit-scrollbar-track {
  background-color: var(--c-scrollbar-track);
}

.select-option .select-option-items .select-option-item {
  align-items: center;
  height: 48px;
  padding-left: 16px;
  padding-right: 16px;
  display: flex;
}

.select-option .select-option-items .select-option-item--selected {
  color: #fff;
  background-color: #1e90ff;
}

.select-option .select-option-items .select-option-item:not(.select-option .select-option-items .select-option-item--selected):hover {
  background-color: #f5f5f5;
}

.select-option--downstairs .select-option-items {
  margin-top: unset;
  margin-bottom: 4px;
  top: auto;
  bottom: 100%;
}

.select-option--open .select-option-items {
  display: block;
}

.select-option select {
  display: none;
}
